/* App.css */
* {
  text-align: center;
  margin: 0;
  padding: 0;
}
body {
  background-color: rgba(201, 64, 167, 0.382);
}
::-webkit-scrollbar {
  display: none;
}
header img {
  height: 200px;
  width: auto;
  position: absolute;
  left: 40px;
  top: -10px;
}
.container {
  margin: auto;
  text-align: center;
}
ul {
  list-style-type: none;
  display: flex;
  justify-content: center;
  cursor: pointer;
  padding: 59px;
}
main {
  padding: 30px 0;
}
li {
  text-align: center;
  font-size: 18px;
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  text-shadow: 0.1px 0.1px 0.6px black;
  padding: 15px;
}
header {
  background-color: rgba(255, 255, 255, 0.399);
  font-size: 20px;
  text-align: center;
  max-width: 96vw;
  margin: auto;
  border-radius: 5px;
  margin-top: 8px;
}
#features {
  max-width: 89vw;
  height: 80vh;
  margin: auto;
  background-image: url(/src/assets/images/candleimage.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 20px;
  margin-bottom: 50px;
  text-shadow: 1px 1px 4px white;
  display: flex;
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana;
}
#features:hover {
  transform: scale(1.1);
}
#features h1 {
  font-size: 40px;
  margin-bottom: 20px;
}
#features button {
  height: 40px;
  width: 90px;
  border-radius: 10px;
  margin-top: 50px;
  background-color: beige;
}
#features button:hover {
  background-color: #e78282;
  cursor: pointer;
}
#custom-section {
  max-width: 89vw;
  height: 60vh;
  margin: auto;
  background-image: url(/src/assets/images/neutralcolors.png);
  background-size:cover;
  background-repeat: no-repeat;
  border-radius: 20px;
  text-shadow: 1px 1px 4px white;
  margin-bottom: 50px;
  border: 1px solid gray;
  display: flex;
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana;
}
#custom-section:hover {
  transform: scale(1.1);
}
#custom-section h2 {
  font-size: 40px;
  margin-bottom: 25px;
}
#custom-section h3 {
  font-size: 25px;
  margin-bottom: 8px;
}
#custom-section h4 {
  font-size: 18px;
  margin-bottom: 5px;
}
#custom-section button {
  height: 40px;
  width: 90px;
  border-radius: 10px;
  margin-top: 30px;
  background-color: beige;
}
.best-seller-text {
  text-align: center;
  margin-bottom: 50px;
}
#best-sellers {
  max-width: 89vw;
  margin: auto;
  padding: 20px 0;
  background-color: rgba(255, 255, 255, 0.598);
  border-radius: 20px;
}
#best-sellers:hover {
  transform: scale(1.1);
}
#best-sellers h2 {
  text-align: center;
  font-size: 1.5rem;
  margin-bottom: 20px;
}

.best-seller-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* 2 columns with equal width */
  gap: 20px; /* Gap between grid items */
}

.best-seller {
  text-align: center;
}

.best-seller img {
  max-width: 100%;
  height: auto;
  border-radius: 8px; /* Rounded corners for images */
  margin-top: 10px;
}

.best-seller-text {
  margin-top: 10px;
}
footer {
  background-color: #333;
  color: #fff;
  text-align: center;
  padding: 20px 0;
}